.btn {
  --mat-btn-bg: #ededed;
  --mat-btn-color: #0f172a;
  --mat-btn-border: currentColor;
  --mat-btn-hover-bg: var(--mat-btn-bg);
  --mat-btn-radius: 0.25rem;
  --mat-btn-y-padding: 0.8rem;
  --mat-btn-x-padding: 2rem;
  --mat-btn-text-size: 1rem;
  --mat-btn-text-weight: 600;
  --mat-btn-shadow-size: 0.35em;
  --mat-btn-shadow-hover-size: 0.2em;
  --mat-btn-shadow-active-size: 0em;
  background-color: var(--mat-btn-bg);
  border-radius: var(--mat-btn-radius);
  padding: var(--mat-btn-y-padding) var(--mat-btn-x-padding);
  color: var(--mat-btn-color);
  box-shadow: 0 var(--mat-btn-shadow-size) 0 var(--mat-btn-border);
  font-size: var(--mat-btn-text-size);
  font-weight: var(--mat-btn-text-weight);
  border: solid 2px var(--mat-btn-border);
  transition: all 0.2s ease-in-out;
  cursor: pointer;
  margin-bottom: 0.6em;
  /* Colors */
}
.btn:hover {
  transform: translateY(calc(var(--mat-btn-shadow-size) - var(--mat-btn-shadow-hover-size)));
  box-shadow: 0 var(--mat-btn-shadow-hover-size) 0 var(--mat-btn-border);
  background-color: var(--mat-btn-hover-bg);
  color: var(--mat-btn-color);
}
.btn:active {
  transform: translateY(calc(var(--mat-btn-shadow-size) - var(--mat-btn-shadow-active-size)));
  box-shadow: 0 var(--mat-btn-shadow-active-size) 0 var(--mat-btn-border);
}
.btn:focus {
  outline: none;
}
.btn[disabled] {
  opacity: 0.65;
  cursor: not-allowed;
  transform: translateY(calc(var(--mat-btn-shadow-size) - var(--mat-btn-shadow-active-size)));
  box-shadow: 0 var(--mat-btn-shadow-active-size) 0 var(--mat-btn-border);
}
