.navbar {
  --mat-navbar-bg: #fff;
  --mat-navbar-text: #0f172a;
  --mat-navbar-border: #d9dcde;
  --mat-navbar-link-hover: #36bfff;
  --mat-navbar-link-active: #36bfff;
  background-color: var(--mat-navbar-bg);
  border-bottom: solid 3px var(--mat-navbar-border);
  display: flex;
  align-items: center;
  width: 100%;
  height: 4.3em;
  padding: 0 1em;
  color: var(--mat-navbar-text);
  transition: all 0.3s ease-in-out;
}
.navbar.dark {
  --mat-navbar-bg: #2e4051;
  --mat-navbar-text: #f5f7f9;
  --mat-navbar-border: #d9dcde;
  --mat-navbar-link-hover: #384a5a;
  --mat-navbar-link-active: #435362;
}
.navbar .links {
  list-style: none;
  display: flex;
  align-items: center;
  white-space: nowrap;
}
.navbar .links .link {
  margin: 0 0.2rem;
  padding: 0.7rem 1rem;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: all 0.15s ease;
  color: var(--mat-navbar-text);
}
.navbar .links .link a {
  text-decoration: none;
  font-weight: bold;
  color: inherit;
}
.navbar .links .link:hover {
  color: var(--mat-navbar-link-hover);
}
.navbar .links .link.active {
  color: var(--mat-navbar-link-active);
}

